<template>
	<!--订单加急查询界面-->
	<div class="d-box v-box">
		<div class="headDiv">
			<div class="headerLeft">
				<span class="spanclass">VIN</span>
				<el-input placeholder="请输入vin码" v-model="head.vin" clearable>
				</el-input>
			</div>

			<div class="headerLeft">
				<span class="spanclass">账户状态</span>
				<el-select v-model="head.urgentStatus" filterable placeholder="请选择加急状态">
					<el-option v-for="item in head.urgentStatusList" :key="item.id" :label="item.name" :value="item.id">
					</el-option>
				</el-select>
			</div>

			<!--安吉显示，经销商／仓库不显示，登录进来的custType-->
			<div class="headerLeft" :style="{display:head.isHidden}">
				<span class="spanclass">经销商/仓库</span>
				<el-autocomplete v-model="dealerName" :fetch-suggestions="querySearch" placeholder="请输入内容" :trigger-on-focus="false" @select="handleSelect" value-key="name"></el-autocomplete>
			</div>
			<div class="clearfix">
				<div class="headRight">
					<SolidButton :type='"solid"' :text='"查询"' @click="queryClick"></SolidButton>
					<SolidButton :type='"hollow"' :text='"重置"' @click="resetClick"></SolidButton>
				</div>
			</div>

			<div class="iconButton clearfix">
				<Button :type='add.type' :icon='add.icon' :text='add.text' @click="exportClick" :loading="exportLoading"></Button>
			</div>
		</div>

		<div class="flexTable">
			<div class="d-box v-box">
				<div class="flexTable">
					<div class="tableDiv">
						<!--3、主内容-->
						<el-table ref="multipleTable" stripe :data="rows" tooltip-effect="dark" style="width: 100%;padding:0 20px;" height="100%" @selection-change="handleSelectionChange" @row-click="toggleSelection">
							<el-table-column type="selection" width="55" align="center"></el-table-column>
							<el-table-column prop="vin" label="VIN" align="center">
							</el-table-column>
							<el-table-column prop="isUrgent" label="加急状态" align="center">
							</el-table-column>
							<el-table-column prop="name" label="经销商" align="center">
							</el-table-column>
						</el-table>
					</div>

				</div>
				<div class="footerDiv">
					<!--4、底部-->
					<el-pagination style="text-align: center;padding: 6px 0px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="pageRows" layout="total,sizes, prev,pager, next,jumper" :total="pageTotal">
					</el-pagination>
				</div>
			</div>
		</div>

	</div>
</template>

<script>
	import Button from '../../button/Button.vue';
	import SolidButton from '../../SelfControls/solidButton/solidButton.vue';
	import Table from '../../table/Table.vue';

	export default {
		components: {
			Button,
			SolidButton,
			Table,
		},
		data() {
			return {
				userId: '',
				custType: '', // 客户类别1是安吉物流，2是主机厂，3是经销商
				type: '', // 类别：1是经销商，2是仓库
				//组件化
				add: {
					text: '导出',
					type: 'icon-export',
					icon: 'icon-export',
				},
				//1、head头部
				head: {
					vin: "",
					urgentStatus: "",
					urgentStatusList: [
						{
							id: 1,
							name: "未受理"
						},
						{
							id: 2,
							name: "已受理"
						}
					],
					isHidden: 'inline',

				},
				exportLoading: false, //导出的参数
				receiveIds:[],//导出的加急订单ID数组
				dealerList: [],
				dealerName: '',
				dealerId: '',
				dealerCode: '',
				dealerType: '',
				

				//3、main内容
				rows: [], //所有的data数组
				multipleSelection: [], //列表选择的数组
				ids: [], //for循环后id的合集
				//表格对应的字段
				tableName: [{
						value: 'vin',
						name: "VIN"
					},
					{
						value: 'code',
						name: "加急状态"
					},
					{
						value: 'pname',
						name: '经销商'
					}
				],
				//4、底部分页
				currentPage: 1, //当前页数
				pageRows: 10, //每页
				pageTotal: 0, //总数

			}
		},
		//3、计算属性（有些方法可以写在公用js里,默认只有getter方法）
		computed: {},
		//4、事件监听
		watch: {},
		//5、筛选
		filters: {},
		//6、方法
		methods: {
			getCusType() {
				var self = this;
				//1、列表接口
				var p = {
					id: self.userId
				}
				self.$api.post('/crm/customer/getByPrimaryKey', p, r => {
					console.log(r);
					self.type = r.repData.type;
					self.dealerType = r.repData.type;
					self.custType = r.repData.custType;
					if(self.custType == 1 || self.custType == 2) {
						self.head.isHidden = 'inline';
					} else {
						self.head.isHidden = 'none';
					}
					//获取到type之后再调列表接口
					self.loadData();
				}, e => {
					console.log(e);
				})
			},
			loadData() {
				var self = this;
				//1、列表接口
				var p = {
						pageNum: self.currentPage,
						pageRows: self.pageRows,
						vin: self.head.vin,
						isUrgent: self.head.urgentStatus,
						custtype: self.custType,
						type: self.dealerType,
						dealerCode: self.dealerCode,
						dealerId : self.dealerId
					}

				self.$api.post('/crm/orderUrgent/getList', p, r => {
					console.log(r);
					self.rows = r.repData.list
					self.pageTotal = r.repData.total
				}, e => {
					console.log(e);
				})
			},
			//1、head
			queryClick() {
				var self = this;
				self.loadData();
			},
			resetClick() {
				var self = this;
				self.head.vin = "";
				self.head.urgentStatus = "";
				self.dealerName = '';
				self.dealerType = self.type;
				self.dealerCode = '';
				self.dealerId = '';
				self.loadData();
			},
			//搜索的input
			querySearch(queryString, cb) {
				var self = this;
				var cbList = [];
				if(queryString.length == 0) {
					cb(cbList);
					return;
				}
				console.log(queryString, cb);
				// 调用 callback 返回建议列表的数据
				var self = this;
				//1、列表接口
				var p = {
					conditions: queryString
				}
				self.$api.post('/crm/orderUrgent/getDealers', p, r => {
					console.log(r);
					self.dealerList = r.repData.list;
					var results = queryString ? self.dealerList.filter(self.createFilter(queryString)) : self.dealerList;
					cb(results);
				}, e => {
					console.log(e);
					cb(cbList);
				})

			},
			//筛选条件
			createFilter(queryString) {
				return(resolve) => {
					return(resolve.name.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
				};
			},
			loadAll() {
				var self = this;
				//1、列表接口
				var p = {
					conditions: queryString
				}
				self.$api.post('/crm/orderUrgent/getDealers', p, r => {
					console.log(r);
					self.dealerList = r.repData.list;
				}, e => {
					console.log(e);
				})
			},
			//选择
			handleSelect(item) {
				var self = this;
				console.log(item);
				self.dealerId = item.id;
				self.dealerCode = item.code;
				self.dealerType = item.type;
			},
			//2、列表头部
			exportClick() {
				if(this.multipleSelection.length == 0) {
					this.$message({
						message: '未勾选数据',
						type: 'warning'
					});
				} else {
					this.$confirm('确定导出勾选数据吗？', '', {
						confirmButtonText: '是',
						cancelButtonText: '否',
						type: '',
						center: true
					}).then(() => {
						this.exportMethod();
					}).catch(() => {});
				}
			},

			exportMethod() {
				var self = this;
				//参数赋空
				self.receiveIds = [];
				for(var i = 0; i < self.multipleSelection.length; i++) {
					self.receiveIds.push(self.multipleSelection[i].id)
				}
				//1、列表接口
				var p = {
						vin: self.head.vin,
						isUrgent: self.head.urgentStatus,
						custtype: self.custType,
						type: self.dealerType,
						dealerCode: self.dealerCode,
						dealerId : self.dealerId,
						receiveIds: self.receiveIds
				}
				self.$api.post('/crm/excle/exportUrgentOrders', p, r => {
					console.log(r);
				}, e => {
					console.log(e);
				})
			},
			formatJson(filterVal, jsonData) {
				return jsonData.map(v => filterVal.map(j => v[j]))
			},
			//3、main内容
			//select行改变
			handleSelectionChange(val) {
				this.multipleSelection = val;
				console.log(val)
			},
			//点击行选中复选框
			toggleSelection(row) {
				var rows = [];
				rows.push(row);
				if(rows) {
					rows.forEach(row => {
						this.$refs.multipleTable.toggleRowSelection(row);
					});
				} else {
					this.$refs.multipleTable.clearSelection();
				}
			},
			//4、footer分页
			//每页多少条数据
			handleSizeChange(val) {
				var self = this;
				console.log("每页" + val);
				self.pageRows = val;
				self.loadData();
			},
			//当前第几页
			handleCurrentChange(val) {
				var self = this;
				console.log("当前页" + val);
				self.currentPage = val;
				self.loadData();
			}

		},
		//7、生命周期
		created() {
			var self = this;
			var user = self.$api.sGetObject('_user');
			self.userId = user.userId;
			self.getCusType(); //先取得type。里面才调取列表和模糊搜索列表		
		},
		mounted() {
			//这里可以使用$el
		}
	}
</script>

<style scoped="scoped">
	/*0、整体flex布局*/
	
	.d-box {
		display: -moz-box;
		/* OLD - Firefox 19- (buggy but mostly works) */
		display: -webkit-box;
		/* OLD - iOS 6-, Safari 3.1-6 */
		display: -webkit-flex;
		/* NEW - Chrome */
		display: -ms-flexbox;
		/* TWEENER - IE 10 */
		display: box;
		display: flexbox;
		display: flex;
		/* NEW, Spec - Opera 12.1, Firefox 20+ */
		height: 100%;
		width: 100%;
	}
	
	.v-box {
		-webkit-box-orient: vertical;
		-webkit-box-direction: normal;
		-moz-box-orient: vertical;
		-moz-box-direction: normal;
		-webkit-flex-direction: column;
		-moz-flex-direction: column;
		-ms-flex-direction: column;
		-o-flex-direction: column;
		flex-direction: column;
	}
	
	.headDiv {
		padding: 20px;
		background: #fff;
		overflow: hidden;
		position: relative;
	}
	
	.flexTable {
		-webkit-box-flex: 1;
		-moz-box-flex: 1;
		-ms-flex: 1;
		flex: 1;
		position: relative;
	}
	
	.tableDiv {
		width: 100%;
		height: 100%;
		position: absolute;
		overflow: auto;
	}
	
	.footerDiv {
		height: 40px;
		position: relative;
	}
	/*1、head头部*/
	/*head-left内容*/
	
	.headerLeft {
		width: auto;
		height: 40px;
		float: left;
		margin-right: 20px;
	}
	
	.spanclass {
		display: inherit;
		line-height: 40px;
		margin-right: 8px;
		float: left;
	}
	
	.headerLeft .el-input {
		width: 188px;
		height: 40px;
	}
	/*head-right内容*/
	
	.headRight {
		float: right;
	}
	/*head按钮*/
	
	.iconButton {
		float: left;
		padding-top: 20px;
	}
	/*可搜索的输入框*/
	
	.demo-autocomplete {
		display: inline-block;
	}
	/*3、窗口*/
	
	.el-form-item {
		width: 49%;
		float: left;
	}
	
	.lastformItem {
		margin-left: 10px;
	}
</style>


